/* General Styling */

  
  .login, .cart {
    color: #000; /* Black text color */
    text-decoration: none;
  }
  
  
  
  /* Product Card Styling */
  .product-card {
    display: flex;
    flex-direction: row; /* Align image and text horizontally */
    align-items: center;
    border: 1px solid #ddd;
    border-radius: 6px; /* Softer corners */
    padding: 0.75rem; /* Reduced padding */
    background-color: #fff;
    transition: box-shadow 0.3s ease;
    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1); /* Subtle shadow */
    width: 100%; /* Full width of the grid column */
    max-width: 400px; /* Optional: Set a maximum width */
  }
  
  .product-card:hover {
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.15); /* Increased shadow on hover */
  }
  
  /* Product Image Styling */
  .product-image-container img {
    width: 80px; /* Smaller image size */
    height: auto;
    border-radius: 4px; /* Rounded corners for images */
    margin-right: 0.75rem; /* Space between image and text */
  }
  .product-card {
    border: 1px solid #ddd;
    border-radius: 5px;
    padding: 15px;
    text-align: center;
    background-color: #fff;
  }
  
  .product-card img {
    width: 100%;
    height: auto;
    border-bottom: 1px solid #ddd;
    margin-bottom: 10px;
  }
  
  .product-card h2 {
    font-size: 1.2rem;
    margin: 10px 0;
  }
  
  .product-card p {
    font-size: 1rem;
    color: #333;
  }
  
  .product-card button {
    padding: 10px 20px;
    background-color: #007bff;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .product-card button:disabled {
    background-color: #ccc;
    cursor: not-allowed;
  }
  
  /* Product Info Styling */
  .product-info {
    text-align: left; /* Align text to the left */
    display: flex;
    flex-direction: column; /* Stack text elements vertically */
  }
  
  .product-name {
    font-size: 1rem; /* Adjusted font size */
    margin-bottom: 0.25rem;
  }
  
  .product-rating {
    font-size: 0.9rem;
    color: #ffba00; /* Star color */
    margin-bottom: 0.25rem;
  }
  
  .product-price {
    font-size: 1.1rem;
    color: #333;
    margin-bottom: 0.5rem;
  }
  
  
  
  /* Filter and Sorting Section */
  .filter-sort-section {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 1.5rem;
    padding: 0 2rem; /* Padding to match product card margin */
  }
  
  /* Cart Styling */
  .cart {
    padding: 1rem;
    background-color: #fff;
    border-radius: 4px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  }
  
  .cart h1 {
    font-size: 1.5rem;
    margin-bottom: 1rem;
  }
  
  .cart-item {
    margin-bottom: 1rem;
    padding-bottom: 1rem;
    border-bottom: 1px solid #ddd;
  }
  
  .cart-item:last-child {
    border-bottom: none;
  }
  
  .cart-item h2 {
    font-size: 1.2rem;
    margin: 0 0 0.5rem 0;
  }
  
  .cart-item p {
    margin: 0.25rem 0;
  }
  
  /* Empty cart message */
  .cart-empty {
    font-style: italic;
    color: #888;
  }
  
  /* Responsive Design */
  @media (max-width: 768px) {
    .products {
      grid-template-columns: 1fr; /* One item per row on smaller screens */
    }
  
    .navbar {
      flex-direction: column;
      align-items: flex-start;
    }
  
    .nav-links {
      flex-direction: column;
      gap: 0.5rem;
    }
  
    .filter-sort-section {
      flex-direction: column;
      align-items: flex-start;
    }
  }